import Vue from "vue";
import VueRouter from "vue-router";
import FrontPage from "../components/FrontPage/FrontPage.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "FrontPage",
    meta: { layout: 'main' },
    component: FrontPage,
  },
  {
    path: "/team",
    name: "members",
    meta: { layout: 'main' },
    component: () => import("../components/FrontPage/Members.vue"),
  },
  {
    path: "/about-us",
    name: "about-us",
    meta: { layout: 'main' },
    component: () => import("../components/FrontPage/WhyUs.vue"),
  },
  {
    path: "/features",
    name: "features",
    meta: { layout: 'main' },
    component: () => import("../components/FrontPage/CardsOpportunity.vue"),
  },

  {
    path: "/services",
    name: "services",
    meta: { layout: 'main' },
    component: () => import("../components/FrontPage/Needs.vue"),
  },

];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
